"use strict";

export default function main() {
  let cameraButton = document.querySelector("#start-camera");
  let video = document.querySelector("#video");
  let startButton = document.querySelector("#start-record");
  let stopButton = document.querySelector("#stop-record");
  let downloadLink = document.querySelector("#download-video");

  let cameraStream = null;
  let mediaRecorder = null;
  let blobsRecorded = [];

  cameraButton.addEventListener("click", async () => {
    cameraStream = await navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true,
    });
    video.srcObject = cameraStream;
  });

  startButton.addEventListener("click", function () {
    // set MIME type of recording as video/webm
    mediaRecorder = new MediaRecorder(cameraStream, { mimeType: "video/webm" });

    // event : new recorded video blob available
    mediaRecorder.addEventListener("dataavailable", function (e) {
      blobsRecorded.push(e.data);
    });

    // event : recording stopped & all blobs sent
    mediaRecorder.addEventListener("stop", function () {
      // create local object URL from the recorded video blobs
      let video_local = URL.createObjectURL(
        new Blob(blobsRecorded, { type: "video/webm" })
      );
      downloadLink.href = video_local;
    });

    // start recording with each recorded blob having 1 second video
    mediaRecorder.start(1000);
  });

  stopButton.addEventListener("click", function () {
    mediaRecorder.stop();
  });
}
